*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

.mdc-radio .mdc-radio__native-control:enabled:checked + .mdc-radio__background .mdc-radio__outer-circle {
  border-color: #3f51b5; }

.mdc-radio .mdc-radio__native-control:enabled + .mdc-radio__background .mdc-radio__inner-circle {
  background-color: #3f51b5; }

.mdc-radio .mdc-radio__background::before {
  background-color: #3f51b5; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  background-color: #eee;
  font-size: 16px;
  font-family: "Lato", "Lucida Grande", "Lucida Sans Unicode", Tahoma, Sans-Serif; }

h2 {
  font-weight: 300;
  letter-spacing: 3px;
  color: black;
  text-transform: uppercase;
  word-break: break-word; }

.mdc-tab-bar {
  margin-left: 0; }

.panel {
  display: none; }

.mdc-card {
  background: white; }

.mdc-card__primary > img {
  display: block;
  max-width: 300px;
  max-height: 300px;
  width: auto;
  height: auto;
  margin: auto; }

.mdc-card__primary > p {
  text-align: center; }

.m-p-g__thumbs .m-p-g__thumbs-img:first-child {
  width: 100%; }

.m-p-g__thumbs .m-p-g__thumbs-img:nth-child(2),
.m-p-g__thumbs .m-p-g__thumbs-img:nth-child(3) {
  width: 50%; }

.m-p-g__thumbs .m-p-g__thumbs-img {
  float: none;
  width: 33%; }

.panel.active {
  display: block; }

table {
  color: rgba(0, 0, 0, 0.87); }

table tr td {
  line-height: 30px;
  border-radius: 2px;
  color: rgba(0, 0, 0, 0.87);
  border-bottom: solid 1px #DDDDDD; }

table td:first-child,
table th:first-child {
  padding: 0 0 0 24px; }

.dataTable-container {
  overflow: scroll; }

.dataTable-top,
.dataTable-bottom {
  color: rgba(0, 0, 0, 0.54); }

.dataTable-info {
  display: inline-block;
  margin: 7px 15px; }

.expand + .mdc-list {
  max-height: 500px; }

.material-icons {
  vertical-align: middle;
  transition: all 0.4s ease; }

.material-icons-set {
  white-space: nowrap; }

.material-icons-set--green .material-icons {
  color: #98c011; }

.expand > .material-icons {
  transform: rotate(90deg); }

.mdc-list {
  overflow: hidden;
  max-height: 0px;
  transition: all 0.4s ease; }

.list-item-slider {
  padding: 40px 15px 30px; }

.price-link {
  font-size: 1.25rem;
  margin: 0 10px;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  font-weight: bold;
  color: #000; }
